import React from 'react'
import { CButton, CCard, CCardBody, CCol, CContainer, CRow } from '@coreui/react'
import fetLogo from '@assets/images/logo.png'
import { useMsal } from '@azure/msal-react'
import { loginRequest } from '../../../auth/authConfig'

const Login = () => {
  const { instance } = useMsal()

  const handleLogin = (loginType) => {
    if (loginType === 'popup') {
      instance.loginPopup(loginRequest).catch((e) => {
        console.log(e)
      })
    } else if (loginType === 'redirect') {
      instance.loginRedirect(loginRequest).catch((e) => {
        console.log(e)
      })
    }
  }

  return (
    <div className="bg-light min-vh-100 d-flex flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md={4}>
            <div className="text-center mb-3">
              <img src={fetLogo} alt="logo" width={200} />
              <h3 className="mt-2">LSP後台</h3>
            </div>
            <CCard className="py-3">
              <CCardBody className="text-center">
                <p>請選擇登入方式</p>
                <CButton className="w-100" onClick={() => handleLogin('popup')}>
                  Azure
                </CButton>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  )
}

export default Login
